<template>
  <div class="show-data">
    <h1>Bewertungen</h1>
    <p>Schreiben Sie eine Bewertung oder speichern Sie bereits erstellte Bewertungen auf Ihrem Gerät.</p>

    <details>
      <summary>
        Trackpoints: {{ numTrackpoints }}
      </summary>
      <table>
      <thead>
        <tr>
          <th>
            Länge
          </th>
          <th>
            Breite
          </th>
          <th>
            Zeitpunkt
          </th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="tp in trackpoints" :key="tp.id">
          <td>
            {{ tp.latLng['lng'] | roundTo3 }}
          </td>
          <td>
            {{ tp.latLng['lat'] | roundTo3 }}
          </td>
          <td>
            {{ tp.id | asTime }}
          </td>
        </tr>
      </tbody>
    </table>
    </details>

    <details>
      <summary>
        Bewertungen: {{ numCollectedPoints }}
      </summary>
      <table>
      <thead>
        <tr>
          <th>
            Läng./Breit.
          </th>
          <th>
            Zeitpunkt
          </th>
          <th>
            Museum
          </th>
          <th>
            Benutzer
          </th>
          <th>
            Eintrittspreis
          </th>
          <th>
            Dauer
          </th>
          <th>
            Museumsgrösse
          </th>
          <th>
            Audioguide
          </th>
          <th>
            Barrierefreiheit
          </th>
          <th>
            Museumsshop
          </th>
          <th>
            Café
          </th>
          <th>
            Sitzgelegenheiten
          </th>
          <th>
            Parkplätze
          </th>
          <th>
            ÖV-Erreichbarkeit
          </th>
          <th>
            Kinderfreundlichkeit
          </th>
          <th>
            Grossgruppen
          </th>
          <th>
            Im Freien
          </th>
          <th>
            Hunde
          </th>
          <th>
            Jugendliche
          </th>
          <th>
            Erwachsene
          </th>
          <th>
            Familien
          </th>
          <th>
            Studenten
          </th>
          <th>
            Experten
          </th>
          <th>
            Bewertung Ausstellung
          </th>
          <th>
            Bewertung Infrastruktur
          </th>
          <th>
            Weiterempfehlung
          </th>
          <th>
            Kommentare
          </th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="cp in collectedPoints" :key="cp.id">
          <td>
            {{ cp.latLng['lng'] | roundTo3 }}/{{ cp.latLng['lat'] | roundTo3 }}
          </td>
          <td>
            {{ cp.id | asShortTime }}
          </td>
          <td>
            {{ cp.museum_id }}
          </td>
          <td>
            {{ cp.user_id }}
          </td>
          <td>
            {{ cp.price }}
          </td>
          <td>
            {{ cp.dwell_time }}
          </td>
          <td>
            {{ cp.size_building }}
          </td>
          <td>
            {{ cp.audioguide }}
          </td>
          <td>
            {{ cp.step_free_access }}
          </td>
          <td>
            {{ cp.shop }}
          </td>
          <td>
            {{ cp.cafe }}
          </td>
          <td>
            {{ cp.seats }}
          </td>
          <td>
            {{ cp.parking }}
          </td>
          <td>
            {{ cp.public_transport }}
          </td>
          <td>
            {{ cp.child_friendly }}
          </td>
          <td>
            {{ cp.big_groups }}
          </td>
          <td>
            {{ cp.outdoor }}
          </td>
          <td>
            {{ cp.dogs }}
          </td>
          <td>
            {{ cp.teenagers }}
          </td>
          <td>
            {{ cp.adults }}
          </td>
          <td>
            {{ cp.families }}
          </td>
          <td>
            {{ cp.students }}
          </td>
          <td>
            {{ cp.experts }}
          </td>
          <td>
            {{ cp.exh_rating }}
          </td>
          <td>
            {{ cp.inf_rating }}
          </td>
          <td>
            {{ cp.recommend }}
          </td>
          <td>
            {{ cp.comments }}
          </td>
        </tr>
      </tbody>
    </table>
    </details>
    
    <div class="button-bar">
      <button class="collect_btn" @click="startCollecting($event)"><i class="fa fa-plus" style="font-size:32px"></i></button>
      <button class="btn" @click="downloadData()"><i class="fa fa-download" style="font-size:32px"></i></button>
      <button class="d_btn" v-on:click="securityQuestion"><i class="fa fa-trash-o" style="font-size:32px"></i></button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ShowData',
  props: ['trackpoints', 'collectedPoints'],
  computed: {
    numTrackpoints () {
      return this.trackpoints.length
    },
    numCollectedPoints () {
      return this.collectedPoints.length
    }
  },
  methods: {

    securityQuestion: function () {
      var confirmation = confirm("Wollen Sie die Einträge wirklich löschen?");
      if (confirmation) {
        this.$emit('delete-all');
      }
    },

    startCollecting (event) {
      event.stopPropagation()
      this.$router.push({ path: '/collect' })
    },
    
    downloadData () {
      var output = "type;time;lon;lat;person_id;museum_id;dwell;size_building;entrance_fee;audioguide;step_free_access;shop;cafe;seats;parking;public_transport;child_friendly;big_groups;outdoor;dogs;teenagers;adults;families;students;experts;exhib;infra;prob_recom;comments;%0D%0A";
      let trackpoints = JSON.parse(localStorage['trackpoints']);
      for (var i = 0; i < trackpoints.length; i++) {
        output += 'trackpoint;' + trackpoints[i].id + ';' + trackpoints[i].latLng['lng'] + ';' + 
          trackpoints[i].latLng['lat'] + ';;%0D%0A';
      }
      let collectedPoints = JSON.parse(localStorage['collectedPoints']);
      for (i = 0; i < collectedPoints.length; i++) {
        console.log(collectedPoints)
        output += 'collectedPoint;' + collectedPoints[i].id + ';' + collectedPoints[i].latLng['lng'] + ';' + 
          collectedPoints[i].latLng['lat'] + ';' + collectedPoints[i].user_id + ';' + collectedPoints[i].museum_id + ';' +
          collectedPoints[i].dwell_time + ';' + collectedPoints[i].size_building + ';' + collectedPoints[i].price + ';' + collectedPoints[i].audioguide + ';' + 
          collectedPoints[i].step_free_access + ';' + collectedPoints[i].shop + ';' + collectedPoints[i].cafe + ';' + collectedPoints[i].seats + ';' + 
          collectedPoints[i].parking + ';' + collectedPoints[i].public_transport + ';' + collectedPoints[i].child_friendly + ';'+ 
          collectedPoints[i].big_groups + ';'+ collectedPoints[i].outdoor + ';'+ collectedPoints[i].dogs + ';' + collectedPoints[i].teenagers + ';' + 
          collectedPoints[i].adults + ';' + collectedPoints[i].families + ';' + collectedPoints[i].students + ';' + collectedPoints[i].experts + ';' + 
          collectedPoints[i].exh_rating + ';' + collectedPoints[i].inf_rating + ';' + collectedPoints[i].recommend + ';' + collectedPoints[i].comments + ';%0D%0A';
      }
      var link = document.createElement('a');
      link.href = "data:text/csv;charset=UTF-8," + output;
      link.download = 'bewertungen.csv';
      link.click();
    } 
  }
}
</script>

<style scoped>
.show-data {
  padding: 24px;
}

table {
  width: 100%;
  text-align: left;
  display: block;
  overflow-x: auto;
  white-space: nowrap;
  margin-bottom: 18px;
  max-height: 250px;
  overflow:auto;
  border: 1px solid grey;
  border-radius: 8px;
}

td, th { 
    padding-left: 12px;
}

.button-bar {
  margin-top: 8px;
  display: flex;
  gap: 16px;
}

button.btn {
  
  padding: 12px;
  background-color: #990000;
  border: 1px solid #990000;
  border-radius: 12px;
  cursor: pointer;
  color: white;
  flex: 1 1 auto;
  font-size: medium;
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.5);
  outline: none;
}

button.collect_btn {
  width: 60px;
  padding: 12px;
  background-color: #990000;
  border: 1px solid #990000;
  border-radius: 30px;
  cursor: pointer;
  color: white;
  /*flex: 1 1 auto;*/
  font-size: medium;
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.5);
  outline: none;
}

button.d_btn {
  width: 60px;
  padding: 12px;
  background-color: black;
  border: 1px solid black;
  border-radius: 30px;
  cursor: pointer;
  color: white;
  /*flex: 1 1 auto;*/
  font-size: medium;
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.5);
  outline: none;
}

h1 {
  margin-top: 0px;
}

summary {
  outline: none;
  font-size: x-large;
  font-weight: bold;
  background-color: #f1f1f1;
  padding: 8px;
  padding-left: 12px;
  margin-bottom: 12px;
  border-radius: 8px;
  display: flex;
  gap: 12px;
}

#description {
  margin-bottom: 12px;
  padding-left: 12px;
  padding-right: 12px;
}

details summary::-webkit-details-marker {
  display:none;
}

summary::-webkit-details-marker,	
summary::marker {
  margin-top: 6px;
  margin-left: 4px;
}	

</style>
