<template>
  <div class="main-map">
    <l-map ref="map" :zoom.sync="zoom" :center.sync="center" :minZoom="12" :maxZoom="18" :max-bounds="maxBounds" :bounds="bounds">
      <l-control-scale position="topright" :imperial="false" :metric="true"></l-control-scale>
      <l-tile-layer :url="url" :attribution="attribution"></l-tile-layer>
      <l-circle-marker ref="location_marker" :lat-lng="currLatLng" :zIndex="9999" :radius="10" :color="'white'" :fill-color="'#0073e6'" :fill-opacity="1"></l-circle-marker>
      <l-marker ref="museum_marker" v-for="aM in allMuseumsData" :key="aM.id" 
        :lat-lng="{lat: aM.geometry.coordinates[1], lng: aM.geometry.coordinates[0]}" :color="'black'" :fill-color="'white'" 
        :fill-opacity="1" :zIndex="1" :icon="icon_weiss">
        <l-popup>
          <div class="popup_container">
            <div class="popup_left">
              <input type="image" id="googleNavi" src="Google-Maps-Icon.png" @click="startNavi(currLatLng.lat, currLatLng.lng, aM.geometry.coordinates[1], aM.geometry.coordinates[0])">
            </div>
            <div class="popup_rigth_bold">
              {{ aM.properties.name_museum }}
            </div>
          </div>
        </l-popup>
      </l-marker>
      <l-marker ref="tour_marker" v-for="tD in tourData.slice(1, 10)" :key="tD.id" 
        :lat-lng="tD.latLng" :color="'#990000'" :fill-color="'#990000'" 
        :fill-opacity="1" :zIndex="1000">
        <l-icon  
          :icon-size="icon_Size"
          :icon-anchor="icon_Anchor">
          <div class="headline">{{ tD.user_sequence }}&thinsp;<img id="icon_image" src="museum_rot.png"></div>
        </l-icon>
        <l-popup>
          <div class="popup_container">
            <div class="popup_left">
              <input type="image" id="googleNavi" src="Google-Maps-Icon.png" @click="startNavi(currLatLng.lat, currLatLng.lng, tD.latLng.lat, tD.latLng.lng)">
            </div>
            <div class="popup_rigth">
              <div id="bold_title">{{ tD.name }}</div>
              Start: {{ tD.arrival_time }} Uhr<br>
              Ende: {{ tD.go_time }} Uhr<br>
              Eintritt: CHF {{ tD.fee }}
            </div>
          </div>
        </l-popup>
      </l-marker>
      
    </l-map>
    <!--<div class="collect" @click="showPoints()">-->
      
      <!--
      <div class="tourNav">
      -->

      <div id="startTour" @click="goToPreferences()">
        <i class="fa fa-location-arrow"></i>
      </div>
      <div id="showTour" @click="goToTour()">
        Tour anzeigen
      </div>
      <div id="zoomButton" @click="zoomToLocation()">
        <i class="fa fa-street-view"></i>
      </div>
      <!--
      <div class="left">
        Trackpoints: {{ numTrackpoints }}
      </div>
      <div class="right">
        Bewertungen: {{ numCollectedPoints }}
      </div>
      -->
      
      <!--</div>-->
  </div>
</template>

<script>
import {LMap, LTileLayer, LCircleMarker, LPopup, LControlScale, LTooltip, LMarker, LIcon} from 'vue2-leaflet'
import { latLngBounds, latLng } from "leaflet"

export default {
  name: 'Map',
  components: { LMap, LTileLayer, LCircleMarker, LPopup, LControlScale, LTooltip, LMarker, LIcon},
  props: ['currLatLng', 'trackpoints', 'collectedPoints', 'tourData', 'allMuseumsData'],
  data() {
    return {
      zoom:12,
      center: {lat: 47.367484, lng: 8.542788},
      url: 'https://stamen-tiles-{s}.a.ssl.fastly.net/toner-lite/{z}/{x}/{y}{r}.png',
      attribution: 'Map tiles by <a href="http://stamen.com">Stamen Design</a>, <a href="http://creativecommons.org/licenses/by/3.0">CC BY 3.0</a> &mdash; Map data &copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors',
      bounds: latLngBounds([
        [47.427756, 8.459325],
        [47.315776, 8.611546]
      ]),
      maxBounds: latLngBounds([
        [47.427756, 8.459325],
        [47.315776, 8.611546]
      ]),
      icon_weiss: L.icon({
        iconUrl: 'museum_weiss.png',
        iconSize: [30, 30],
        iconAnchor: [15, 15]
      }),
      icon_Size: [60, 30],
      icon_Anchor: [45, 28]
    }
  },
  methods: {
    zoomToLocation() {
      //console.log(this.currLatLng)
      this.$refs.map.setZoom(16)
      this.$refs.map.setCenter(this.currLatLng)
    },
    showPoints () {
      this.$router.push({ path: '/show-data' })
    },
    startCollecting (event) {
      event.stopPropagation()
      this.$router.push({ path: '/collect' })
    },
    goToTour () {
      if (this.tourData.length > 0) {
        this.$router.push({ path: '/tour' })
      } else {
        alert('Es wurde noch keine Tour erstellt. Geben Sie zuerst Ihre Präferenzen ein.')
        this.$router.push({ path: '/preferences' })
      }
    },
    goToPreferences () {
      if (this.tourData.length == 0) {
        this.$router.push({ path: '/preferences' })
      } else {
        var confirmation = confirm("Wollen Sie Ihre aktuelle Tour überschreiben?");
        if (confirmation) {
          this.$emit('delete-tour');
          this.$router.push({ path: '/preferences' })
        }
      }
    },
    startNavi (lat1, lng1, lat2, lng2) {
      console.log('Navi reached')
      var baseUrl = 'https://www.google.com/maps/dir/';
      var concatUrl = baseUrl.concat(lat1,',',lng1,'/',lat2,',',lng2,'/')
      console.log(concatUrl)
      var win = window.open(concatUrl, '_blank');
      win.focus();
    },
  },
  computed: {
    numTrackpoints () {
      return this.trackpoints.length
    },
    numCollectedPoints () {
      return this.collectedPoints.length
    }
  }
}
</script>

<style scoped>
.main-map {
  width: 100%;
  height: calc(100% - 0px);
}

.tourNav {
  /* Make sure this is above the map. */
  z-index: 1001;

  position: relative;
  bottom: 0;
  left: 0;
  right: 0;
  height: 0px;
  background-color: #f1f1f1;
  -webkit-box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.25);
  -moz-box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.25);
  box-shadow: 0px 0px 8px 0px rgba(0,0,0,0.5);
  color: #990000;
  font-weight: bold;
}

/*
.collect .left {
  position: absolute;
  left: 24px;
  top: 12px;
  margin: 0;
}

.collect .right {
  position: absolute;
  right: 24px;
  top: 12px;
  margin: 0;
}
*/

#startTour {
  z-index: 1002;
  width: 50px;
  height: 50px;
  border-radius: 25px;
  background-color: #990000;
  position: absolute;
  margin: 0 auto;
  padding-bottom: 2px;
  left: 0;
  right: 260px;
  bottom: 45px;
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: center;
  font-size: 32px;
  -webkit-box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.75);
  -moz-box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.75);
  box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.75);
  color: white;
  font-weight: normal;
}

#showTour {
  z-index: 1002;
  width: 180px;
  height: 50px;
  border-radius: 12px;
  background-color: #f1f1f1;
  position: absolute;
  margin: 0 auto;
  padding-bottom: 2px;
  left: 0;
  right: 0;
  bottom: 45px;
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: center;
  font-size: x-large;
  -webkit-box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.75);
  -moz-box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.75);
  box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.75);
  color: #990000;
  font-weight: bold;
}

#zoomButton {
  z-index: 1002;
  width: 50px;
  height: 50px;
  border-radius: 25px;
  background-color: #0073e6;
  position: absolute;
  margin: 0 auto;
  padding-bottom: 2px;
  left: 0;
  right: -260px;
  bottom: 45px;
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: center;
  font-size: 28px;
  -webkit-box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.75);
  -moz-box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.75);
  box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.75);
  color: white;
  font-weight: normal;
}

.popup_container {
  display: flex;
  gap: 8px;
}

.popup_rigth {
  width: 95%;
  font-size: medium;
}

#googleNavi {
  max-height: 25px;
  vertical-align: middle;
  outline: none;
}

#bold_title {
  font-weight: bold;
  font-size: large;
  margin-bottom: 12px;
}

.popup_rigth_bold {
  width: 95%;
  font-size: large;
  font-weight: bold;
}

#icon_image {
  height: 30px;
  width: 30px;
}

.headline {
  font-size: 40px;
  color: #990000;
  font-weight: bold;
  overflow: hidden;
  white-space: nowrap;
}


</style>
