import Vue from 'vue'
import VueRouter from 'vue-router'
import App from './App.vue'
import routes from './routes.js'
import moment from 'moment'

Vue.config.productionTip = false
moment.locale('de-ch')

Vue.filter('roundTo3', function (value) {
  return Math.round(1e3 * value) / 1e3;
});

Vue.filter('asTime', function (value) {
  return moment(value).format('Do MMMM YYYY, HH:mm:ss');
});

Vue.filter('asShortTime', function (value) {
  return moment(value).format('DD.MM. HH:mm:ss');
});

Vue.use(VueRouter)

const router = new VueRouter({
  routes
})

new Vue({
  render: h => h(App),
  router
}).$mount('#app')
