<template>
  <div class="preferences">
    <h1>Präferenzen festlegen</h1>
    <p>Stellen Sie hier ein, nach welchen Kriterien Ihre Tour erstellt werden soll:</p>
    <form action="">

      <label for="target_group">Zielgruppe</label>
      <select v-model="target_group" id="target_group" name="target_group">
        <option value="teenagers">Jugendliche</option>
        <option value="adults">Erwachsene</option>
        <option value="families">Familien</option>
        <option value="students">Studierende</option>
        <option value="experts">Fachpublikum</option>
      </select>

      <label for="checkbox">Museumskategorie<br></label>
      <input type="checkbox" id="art" name="art" value="True" v-model="art"/>
      <label for="teenagers">Kunst</label><br>
      <input type="checkbox" id="photo" name="photo" value="True" v-model="photo"/>
      <label for="photo">Fotografie</label><br>
      <input type="checkbox" id="design" name="design" value="True" v-model="design"/>
      <label for="design">Design und Architektur</label><br>
      <input type="checkbox" id="culture" name="culture" value="True" v-model="culture"/>
      <label for="culture">Kultur</label><br>
      <input type="checkbox" id="nature" name="nature" value="True" v-model="nature"/>
      <label for="nature">Natur und Technik</label><br><br>
      <hr>

      <label for="step_free">Barrierefreiheit benötigt<br></label>
      <input type="radio" id="yes" name="step_free" value=true v-model="step_free">
      <label for="yes">Ja &nbsp;&nbsp;</label>
      <input type="radio" id="no" name="step_free" value=false v-model="step_free">
      <label for="no">Nein</label><br>
      <hr>

      <label for="startort">Startort</label>
      <select v-model="startort" id="startort" name="startort">
        <option value="akt">aktueller Standort</option>
        <option value="HB">Zürich HB</option>
        <option value="ETHZ">ETH Zentrum</option>
        <option value="ETHH">ETH Hönggerberg</option>
      </select>

      <label for="weekday">Wochentag</label>
      <select v-model="weekday" id="weekday" name="weekday">
        <option value="mon">Montag</option>
        <option value="tue">Dienstag</option>
        <option value="wed">Mittwoch</option>
        <option value="thu">Donnerstag</option>
        <option value="fri">Freitag</option>
        <option value="sat">Samstag</option>
        <option value="sun">Sonntag</option>
      </select>

      <label for="start_time">Startzeit der Tour</label>
      <input type="time" id="start_time" name="start_time" v-model="start_time"/>

      <label for="end_time">Endzeit der Tour</label>
      <input type="time" id="end_time" name="end_time" v-model="end_time"/>
      
      <input type="submit" value="Museen in der Nähe finden" @click="findMuseums()">
    </form>
  </div>
</template>

<script>
export default {
  name: 'Preferences',
  props: ['currLatLng', 'preferenceData', 'tourData'],
  data () {
    return {
      target_group: '',
      art: '',
      photo: '',
      design: '',
      culture: '',
      nature: '',
      step_free: '',
      weekday: '',
      start_time: '',
      end_time: '',
      startort: '',
    }
  },
  methods: {
    findMuseums() {
      event.preventDefault()
      
      var startort_neu;

      switch (this.startort) {
        case 'akt':
          startort_neu = this.currLatLng;
        break;
        case 'HB':
          startort_neu = {lat: 47.377758, lng: 8.540590};
        break;
        case 'ETHZ':
          startort_neu = {lat: 47.376522, lng: 8.548457};
        break;
        case 'ETHH':
          startort_neu = {lat: 47.408044, lng: 8.507216};
        break;
      }
      console.log(startort_neu)

      this.preferenceData.pop()
      this.preferenceData.push({
        latLng: startort_neu,// this.currLatLng
        target_group: this.target_group,
        art: this.art,
        photo: this.photo,
        design: this.design,
        culture: this.culture,
        nature: this.nature,
        step_free: this.step_free,
        weekday: this.weekday,
        start_time: this.start_time,
        end_time: this.end_time,
        startort: startort_neu,
      })

      this.$router.push({ path: '/suggestions' })

      this.$emit('delete-distance_arr');

      //var goTime = this.start_time.split(':');
      //goTime = goTime[0]*3600 + goTime[1]*60;

      this.tourData.pop();
      this.tourData.push({
        id: 1, 
        name: 'Startort',
        latLng: startort_neu,// this.currLatLng
        latLng_before: this.currLatLng,
        //away_time: null,//Entfernung vom vorherigen Museum
        arrival_time: null,//Ankuft
        go_time: this.start_time,//Verlassen
        user_sequence: null,
        fee: null,
      })
    }
  }
}
</script>

