<template>
  <div id="app">
    <app-header></app-header>
    <div class="content">
      <router-view :trackpoints="trackpoints" 
        :collectedPoints="collectedPoints"
        :currLatLng="currLatLng"
        :preferenceData="preferenceData"
        :tourData="tourData"
        :allMuseumsData="allMuseumsData"
        :distance_arr="distance_arr"
        @delete-all="deleteAll()"
        @delete-preferences="deletePreferences()"
        @delete-tour="deleteTour()"
        @delete-distance_arr="deleteDistance_arr()"
        ></router-view><!-- @call-makeSuggestions="callMakeSuggestions()" -->
    </div>
  </div>
</template>

<script>
import AppHeader from './components/AppHeader.vue'
import L from 'leaflet'
import 'leaflet/dist/leaflet.css'
//import {GeoJSON, WFS} from 'ol/format'

// Export default basically means that if this script is imported in another, this
// structure will be imported.
export default {
  name: 'app',
  // Here we define components that appear in the template. While they are written as 
  // AppHeader here, in the HTML template they will be referenced as <app-header>.
  components: {
    AppHeader,
    //SidebarMenu
  },
  // Data specifies the internal state of this component. Vue.js will use this to 
  // compute a dataflow graph.
  data () {
    return {
      currLatLng: L.latLng(47.408375, 8.507669),
      trackpoints: [],
      collectedPoints: [],
      
      preferenceData: [],
      tourData: [], 
      allMuseumsData: [],  
      distance_arr: [],   
    }
  },
  // Defines what should happen when this component is mounted, i.e., added to the DOM.
  mounted () {
    // We load everything from the local storage (lab 2).
    if (localStorage.getItem('collectedPoints')) {
      this.collectedPoints = JSON.parse(localStorage.getItem('collectedPoints'));
    }
    if (localStorage.getItem('trackpoints')) {
      this.trackpoints = JSON.parse(localStorage.getItem('trackpoints'));
    }
    if (localStorage.getItem('preferenceData')) {
      this.preferenceData = JSON.parse(localStorage.getItem('preferenceData'));
    }
    if (localStorage.getItem('tourData')) {
      this.tourData = JSON.parse(localStorage.getItem('tourData'));
    }
    if (localStorage.getItem('allMuseumsData')) {
      this.allMuseumsData = JSON.parse(localStorage.getItem('allMuseumsData'));
    }
    if (localStorage.getItem('distance_arr')) {
      this.distance_arr = JSON.parse(localStorage.getItem('distance_arr'));
    }
    // dieser Teil ist nur zum testen
    /*
    var foo = 0;
    this.tourData.pop();
    this.tourData.push({
      museum: foo
    });
    */
    /*
     * Get Data from Geoserver and save it into 
     */

    let vm = this;
    var featureRequest = new ol.format.WFS().writeGetFeature({
			featurePrefix: "gisuebung16", //<-- GeoServer workspace name
			version: "1.1.0",
			featureTypes: ["all_data"],//<-- layer name
			featureNS: "http://n.ethz.ch/~brunnjoh/gta/gisuebung05",
			srsName: "EPSG:4326",
			bbox: [8.498750880416, 47.3470702, 8.5710711, 47.408010611161],
			geometryName: "position", //<-- geometry field
			outputFormat: "application/json"
			})
	
      const url = "http://ikgeoserv.ethz.ch:8080/geoserver/wfs"
      const proxyurl = "https://cors-anywhere.herokuapp.com/";
      fetch(proxyurl + url, {
        method: 'POST',
        body: new XMLSerializer().serializeToString(featureRequest)
      })
      .then(function(response) {
        
        return response.json();
      })
      .then(function(json) {
        //var features = new ol.format.GeoJSON().readFeatures(json);
        var i;
        vm.allMuseumsData = []
        for(i = 0; i<40; i++){
          //console.log(json['features'][i]);
          vm.allMuseumsData.push(json['features'][i])
        }
        console.log("Loading from Geoserver finished!");
      });
    
    /**
     * What to do when we get a new position update.
     */
    let geoSuccess = (position) => {
      let lat = position.coords.latitude;
      let lng = position.coords.longitude;
      this.currLatLng = L.latLng(lat, lng);

      this.trackpoints.push({
        id: Date.now(),
        latLng: this.currLatLng,
        radius: position.accuracy
      });
    }

    if ("geolocation" in navigator) {
      navigator.geolocation.watchPosition(geoSuccess, function () {}, {});
    }
  },
  // Watch these "data" (component state) properties, and do stuff when they change.
  watch: {
    // Whenever some of the points (the automatically tracked ones or the manually 
    // collected ones) change, we store it to the localstorage.
    collectedPoints: {
      handler () {
        localStorage.setItem('collectedPoints', JSON.stringify(this.collectedPoints));
      }
    },
    trackpoints: {
      handler () {
        localStorage.setItem('trackpoints', JSON.stringify(this.trackpoints));
      }
    },
    preferenceData: {
      handler () {
        localStorage.setItem('preferenceData', JSON.stringify(this.preferenceData));
      }
    },
    tourData: {
      handler () {
        localStorage.setItem('tourData', JSON.stringify(this.tourData));
      }
    },
    allMuseumsData: {
      handler () {
        localStorage.setItem('allMuseumsData', JSON.stringify(this.allMuseumsData));
      }
    },
    distance_arr: {
      handler () {
        localStorage.setItem('distance_arr', JSON.stringify(this.distance_arr));
      }
    },
  },
  // Some general methods that can be used throughout this component.
  methods: {
    deleteAll () {
      this.trackpoints = []
      this.collectedPoints = []
    },
    deleteTour () {
      this.tourData = []
    },
    deletePreferences () {
      this.preferenceData = []
    },
    deleteDistance_arr () {
      this.distance_arr = []
    },
    /*
    callMakeSuggestions () {
      this.$refs.sugg.makeSuggestions();
    }
    */
  }
}
</script>

