  font-family: 'Source Sans Pro', sans-serif;
}

#app {
  color: #2c3e50;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
}

.content {
  flex-grow: 1;
  overflow-y: scroll;
}

</style>
