<script>

export default {
  name: 'Collect',
  props: ['currLatLng', 'collectedPoints'],
  data () {
    return {
      museum_id: '',
      user_id: '',
      price: '',
      dwell_time: '',
      size_building: '',
      audioguide: '',
      step_free_access: '',
      shop: '',
