      seats: '',
      parking: '',
      public_transport: '',
      child_friendly: '',
      big_groups: '',
      outdoor: '',
      dogs: '',
      teenagers: '',
      adults: '',
      families: '',
      students: '',
      experts: '',
      exh_rating: '',
      inf_rating: '',
      recommend: '',
      comments: ''
    }
  },
  methods: {
    /**
     * Specifies what to do when someone clicks the button.
     */
    submitForm (event) {
      event.preventDefault()

      this.collectedPoints.push({
        id: Date.now(),
        latLng: this.currLatLng,
        museum_id: this.museum_id,
        user_id: this.user_id,
        price: this.price,
        dwell_time: this.dwell_time,
        size_building: this.size_building,
        audioguide: this.audioguide,
        step_free_access: this.step_free_access,
        shop: this.shop,
        cafe: this.cafe,
        seats: this.seats,
        parking: this.parking,
        public_transport: this.public_transport,
        child_friendly: this.child_friendly,
        big_groups: this.big_groups,
        outdoor: this.outdoor,
        dogs: this.dogs,
        teenagers: this.teenagers,
        adults: this.adults,
        families: this. families,
        students: this.students,
        experts: this.experts,
        exh_rating: this.exh_rating,
        inf_rating: this.inf_rating,
        recommend: this.recommend,
        comments: this.comments
      })

      this.$router.push({ path: '/show-data' })
    }
  }
}
</script>

