<template>
  <div class="tour">
    <h1>Ihre Museumstour</h1>
    <p>Über den "+"-Button können Sie Ihrer Tour ein weiteres Museum hinzufügen.
    
    <div id="tourListe" v-for="tD in tourData.slice(1, 10)" :key="tD.id">
      <div id="left">
        <div id="heading">{{ tD.name }}</div>
        {{ tD.arrival_time }} Uhr &ndash; {{ tD.go_time }} Uhr<br>
        Eintritt: CHF {{ tD.fee }}
        <!-- Hier noch Entfernung vom vorherigen Museum und Ankunftszeit einfügen -->
      </div>
      <div id="right">
        <input type="image" id="googleNavi" src="Google-Maps-Icon.png" @click="startNavi(tD.latLng_before.lat, tD.latLng_before.lng, tD.latLng.lat, tD.latLng.lng)">
      </div>
    </div> 
    
    <br>
    <div id="add_museum" @click="goToSuggestions()">
        <i class="fa fa-plus" style="font-size:30px"></i>
    </div><br>
    <div id="eintrittspreis">Summe der Eintrittspreise: CHF {{ gesamtpreis }}</div>
    <br>
    <div class="button-bar">
      <div id="saveTour" @click="goToMap()">
        Zurück zur Karte
      </div>
      <div id="dismissTour" @click="clearTour()">
        <i class="fa fa-trash-o"></i>
      </div>
      
    </div>
    
  </div>
</template>

<script>
export default {
  name: 'tour',
  props: ['currLatLng', 'allMuseumsData', 'tourData'],
  data() {
    return {
      gesamtpreis: '',
    }
  },
  created:
    function(){
      this.calcGesamtpreis()
    },
    
  methods: {
    startNavi (lat1, lng1, lat2, lng2) {
      console.log('Navi reached')
      var baseUrl = 'https://www.google.com/maps/dir/';
      var concatUrl = baseUrl.concat(lat1,',',lng1,'/',lat2,',',lng2,'/')
      console.log(concatUrl)
      var win = window.open(concatUrl, '_blank');
      win.focus();
    },
    goToMap () {
      this.$router.push({ path: '/' })
    },
    goToSuggestions () {
      this.$router.push({ path: '/suggestions' })
      this.$emit('delete-distance_arr');
    },
    clearTour () {
      var confirmation = confirm("Wollen Sie Ihre Tour wirklich löschen?");
      if (confirmation) {
        this.$emit('delete-tour');
        this.$router.push({ path: '/' })
      }
    },
    calcGesamtpreis() {
      this.gesamtpreis = 0;
      for (var i = 1; i < this.tourData.length; i++) {
        this.gesamtpreis = this.gesamtpreis + parseFloat(this.tourData[i].fee);
      }
      this.gesamtpreis = this.gesamtpreis.toFixed(1).concat("0");
    }
  }
}
</script>

