<template>
  <div class="museums">
    <h1>Zürichs Museen</h1>
    <p>Für weitere Informationen Museen anklicken.</p>
    <div id="ausklappListe" v-for="aM in allMuseumsData" :key="aM.id">
      <details>
        <summary>
          <div id="left">{{ aM.properties.name_museum }}</div>
          <div id="right"> <input type="image" id="googleNavi" src="Google-Maps-Icon.png" @click="startNavi(aM.geometry.coordinates[1], aM.geometry.coordinates[0])"></div>
        </summary>
        <div id="description">
          <i class="fa fa-star"></i>&nbsp; {{ aM.properties.avg_prob_recom }}/10<br>
          <i class="fa fa-hourglass"></i>&nbsp; {{ aM.properties.avg_dwell }} Minuten<br><!-- hier sollte noch besser gerundet werden -->
          <i class="fa fa-bus"></i>&nbsp; {{ aM.properties.public_transport }} Gehminuten entfernt<br>
          <i class="fa fa-money"></i>&nbsp; CHF {{ aM.properties.avg_fee }} 
          <!-- Liste mit Attributen ausbaufähig -->
        </div>
      </details>
    </div> 
  </div>
</template>

<script>
export default {
  name: 'museums',
  props: ['currLatLng', 'allMuseumsData'],
  methods: {
    startNavi (lat, lng) {
      console.log('Navi reached')
      var baseUrl = 'https://www.google.com/maps/dir/';
      var concatUrl = baseUrl.concat(this.currLatLng.lat,',',this.currLatLng.lng,'/',lat,',',lng,'/')
      console.log(concatUrl)
      var win = window.open(concatUrl, '_blank');
      win.focus();
      //open(concatUrl,"_self")
    },
    jaOderNein(param) {
      if (param == true) {
        return 'Ja'
      } else {
        return 'Nein'
      }
    }
  }
}
</script>

<style scoped>
.museums {
  padding: 24px;
}

h1 {
  margin-top: 0px;
}

summary {
  outline: none;
  font-size: large;
  background-color: #f1f1f1;
  padding: 8px;
  padding-left: 12px;
  margin-bottom: 12px;
  border-radius: 8px;
  display: flex;
  gap: 12px;
}

#googleNavi {
  max-height: 25px;
  vertical-align: middle;
  outline: none;
}

#left {
  width: 95%;
}


#description {
  margin-bottom: 12px;
  padding-left: 12px;
  padding-right: 12px;
}

details summary::-webkit-details-marker {
  display:none;
}

summary::-webkit-details-marker,	
summary::marker {
  margin-top: 6px;
  margin-left: 4px;
}	

</style>
