<template>
  <header>
    <div class="left">
      <router-link to="/" style="font-size:32px">Museumstouren<br>Zur&inodot;ch</router-link>
    </div>
    <div class="right">
      
      <div class="dropdown">
        
        <button class="dropbtn" id="menu_button" v-on:click="showDropdown"><i class="fa fa-bars" style="font-size:32px"></i></button>
          <div class="dropdown-content" id="dropdown_content" v-on:click="hideDropdown">
            <router-link to="/">Karte</router-link>
            <router-link to="/museums">Alle Museen</router-link>
            <router-link to="/show-data">Bewertungen</router-link>
            <router-link to="/about">Über die App</router-link>
          </div>
      </div>
    </div>
  </header>
</template>

<script>
export default {
  name: 'AppHeader',
  methods: {
    
    securityQuestion: function () {
      var confirmation = confirm("Wollen Sie die Einträge wirklich löschen?");
      if (confirmation) {
        this.$emit('delete-all');
      }
    },

    showDropdown: function () {
      var x = document.getElementById("dropdown_content");
      var y = document.getElementById("menu_button");
      if (x.style.display === "none") {
        x.style.display = "block";
        y.style.backgroundColor = "#dfdfdf";
      }  else {
        x.style.display = "none";
        y.style.backgroundColor = "#f1f1f1";
      }
    },

    hideDropdown: function () {
      var x = document.getElementById("dropdown_content");
      var y = document.getElementById("menu_button");
      x.style.display = "none";
      y.style.backgroundColor = "#f1f1f1";
      }
    }
  }

</script>

