<template>
  <div class="suggestions">
    <h1>Museum auswählen</h1>
    <p>Wählen Sie im Dropdown-Menü ein Museum aus, das Sie besuchen möchten und speichern Sie ihre Auswahl.</p>
    <select id="auswahl" v-model="chosenMuseum">    
      <option v-for="dA in distance_arr" :key="dA.id" :value="dA.id">
        <div id="museum_name">{{ dA.name }}</div>
      </option>
    </select>

    <div id="auswahl_suggestions" v-for="dA in distance_arr" :key="dA.id">
        <div id="museumsname">{{ dA.name }}</div>
        <div id="recomAndDist">
          <i class="fa fa-star"></i>&nbsp; {{ dA.recom }}/10&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<i class="fa fa-arrow-right"></i>&nbsp; {{ dA.dist | roundTo3 }} Meter<br><!-- Das ist logischerweise noch nicht optimal -->
        </div>
    </div>

    <div class="button-bar">
      <div id="add_museum" @click="goToTour()">
        Auswahl speichern
      </div>
    </div>
    
  </div>
</template>

<script>
import Vue from 'vue'
export default {
  name: 'suggestions',
  props: ['allMuseumsData', 'preferenceData', 'tourData', 'distance_arr'],
  data() {
    return {
      chosenMuseum: null
    }
  },
  created: 
      function(){
        this.makeSuggestions()
      },
  methods: {
    goToTour () {
      if (this.distance_arr.length > 0) {
        this.$router.push({ path: '/tour' })
        
        var correctIndex = -1;
        var k;
        for (k = 0; k<this.distance_arr.length; k++) {
          if (this.distance_arr[k].id == this.chosenMuseum) {
            correctIndex = k;
          }
        }
        
        var time = this.distance_arr[correctIndex].dist_time;
        time = Math.round(time);

        console.log(correctIndex)
        console.log(time)

        //var quotient = Math.floor(y/x);
        //var remainder = y % x;

        var previousGoTime = this.tourData[this.tourData.length-1].go_time;
        previousGoTime = previousGoTime.split(':');
        previousGoTime = previousGoTime[0]*3600 + previousGoTime[1]*60;
        var arrival_time = previousGoTime + time;
        var standard_arrival_time = this.secondsToTime(arrival_time);

        var departure_time = arrival_time + this.distance_arr[correctIndex].dwell*60;
        var standard_departure_time = this.secondsToTime(departure_time);

        var corr_lat_coord = this.distance_arr[correctIndex].position[1];
        var corr_lng_coord = this.distance_arr[correctIndex].position[0];
        var corr_coord = {'lat': corr_lat_coord, 'lng': corr_lng_coord};
        console.log(corr_coord)

        this.tourData.push({
          id: this.distance_arr[correctIndex].id, 
          name: this.distance_arr[correctIndex].name,
          latLng: corr_coord, //mögliche Fehlerquelle, weil Koordinaten vertauscht sind
          latLng_before: this.tourData[this.tourData.length-1].latLng,
          //away_time: time,//Entfernung vom vorherigen Museum
          arrival_time: standard_arrival_time,//Ankunft
          go_time: standard_departure_time,//Verlassen
          user_sequence: this.tourData.length.toString(),
          fee: this.distance_arr[correctIndex].price,
        })
      } else {
        this.$router.push({ path: '/tour' })
      }
    },
    secondsToTime(seconds) {
      var time1 = Math.floor(seconds/3600);
      var time2 = Math.floor((seconds % 3600)/60);
      time1 = time1.toString();
      time2 = time2.toString();
      if (time2.length < 2) {
        var zero = "0";
        time2 = zero.concat(time2);
      }
      var standard_time = time1.concat(':',time2);
      //console.log(standard_arrival_time)
      return standard_time;
    },
    
    
    /*
     * Fünf Vorschläge basierend auf den Präferenzen und allen Museumsdaten generieren.
     */

    makeSuggestions () {
      console.log("Aufruf von makeSuggestions hat funktioniert!");

      // Elemente aus allMuseumsData und preferenceData adressieren
      let allMuseumsData = JSON.parse(localStorage['allMuseumsData']);
      let preferenceData = JSON.parse(localStorage['preferenceData']);
      //let tourData = JSON.parse(localStorage['tourData']);
      
      //var i;
      /*console.log(preferenceData[0].weekday, preferenceData[0].step_free)

      for (i = 0; i < allMuseumsData.length; i++) {
        console.log(allMuseumsData[i].properties.name_museum)
        console.log(allMuseumsData[i].geometry.coordinates)
      }
      */
      //console.log(allMuseumsData);
      
      /*** 
       * MUSSKRITERIEN 
       ***/

      var i;
      var mlength = allMuseumsData.length;
      //console.log(mlength);
      for (i = 0; i < mlength; i++) {
        if (allMuseumsData[i].properties.avg_prob_recom < 5) {
          allMuseumsData.splice(i, 1);
          i = i-1;
          mlength = mlength-1;
        }
      }
      //console.log(allMuseumsData);
      
      
      //var mlength = allMuseumsData.length;
      for (var i = 0; i < mlength; i++) { 
        //console.log(preferenceData[0].step_free)
        if (preferenceData[0].step_free == 'true') {
          if (allMuseumsData[i].properties.step_free_access == false) {
            //console.log(allMuseumsData[i].properties.step_free_access)
            allMuseumsData.splice(i, 1);
            i = i-1;
            mlength = mlength-1;
          }
        }
      }
      
      //console.log(allMuseumsData);

      //var mlength = allMuseumsData.length;
      for (var i = 0; i < mlength; i++) {
        var categoryBoolean = false;
        if (allMuseumsData[i].properties.art && preferenceData[0].art == true) {
          categoryBoolean = true;
        }
        if (allMuseumsData[i].properties.culture && preferenceData[0].culture == true) {
          categoryBoolean = true;
        }
        if (allMuseumsData[i].properties.natural_tech && preferenceData[0].nature == true) {
          categoryBoolean = true;
        }
        if (allMuseumsData[i].properties.photo && preferenceData[0].photo == true) {
          categoryBoolean = true;
        }
        if (allMuseumsData[i].properties.design_arch && preferenceData[0].design == true) {
          categoryBoolean = true;
        }
        if (categoryBoolean == false) {
          allMuseumsData.splice(i, 1);
            i = i-1;
            mlength = mlength-1;
        }
      }
      // checken, ob diese Schleife auch ihren Dienst tut -> wenn schon Museen in tourData vorhanden sind
      for (var i = 0; i < mlength; i++) {
        for (var j = 0; j < this.tourData.length; j++) {
          if (allMuseumsData[i].properties.museum_id == this.tourData[j].id) {
            allMuseumsData.splice(i, 1);
            i = i-1;
            mlength = mlength-1;
          }
        }
      }
      
      //console.log(allMuseumsData);

      /*** 
       * ERREICHBARKEIT 
       ***/
      
      
      //var open;
      //var close;
        
      for (var i = 0; i < mlength; i++) {
        
        var day = preferenceData[0].weekday;
        switch (day) {
          case 'mon':
            if (allMuseumsData[i].properties.mon_open == null) {
              allMuseumsData.splice(i, 1);
              i = i-1;
              mlength = mlength-1;
            }
          break;
          case 'tue':
            if (allMuseumsData[i].properties.tue_open == null) {
              allMuseumsData.splice(i, 1);
              i = i-1;
              mlength = mlength-1;
            }
          break;
          case 'wed':
            if (allMuseumsData[i].properties.wed_open == null) {
              allMuseumsData.splice(i, 1);
              i = i-1;
              mlength = mlength-1;
            }
          break;
          case 'thu':
            if (allMuseumsData[i].properties.thu_open == null) {
              allMuseumsData.splice(i, 1);
              i = i-1;
              mlength = mlength-1;
            }
          break;
          case 'fri':
            if (allMuseumsData[i].properties.fri_open == null) {
              allMuseumsData.splice(i, 1);
              i = i-1;
              mlength = mlength-1;
            }
          break;
          case 'sat':
            if (allMuseumsData[i].properties.sat_open == null) {
              allMuseumsData.splice(i, 1);
              i = i-1;
              mlength = mlength-1;
            }
          break;
          case 'sun':
            if (allMuseumsData[i].properties.sun_open == null) {
              allMuseumsData.splice(i, 1);
              i = i-1;
              mlength = mlength-1;
            }
          break;
        }
      }

      /*
      Do table "dist" with all distances "dist" between museums and user (Euklidische Distanz?):
	For i in museum:
	  dist[(i[id])[dist]] = dist(user[Standort], museum[Standort])
Do expand "dist" with walking "time":
	For d in dist:
	  dist[(d[id])[time]] = dist[(d[id])[dist]] / geschw.
    */

    // Dieser Teil wurde nach der Haversine Formel berechnet

    //var distance_arr = [];
    for (var i = 0; i < mlength; i++) {
      var lat_start = this.tourData[this.tourData.length-1].latLng.lat;
      var lng_start = this.tourData[this.tourData.length-1].latLng.lng;
      var lat_end = allMuseumsData[i].geometry.coordinates[1];
      var lng_end = allMuseumsData[i].geometry.coordinates[0];
      var R = 6371; // Radius of the earth in km
      var dLat = (lat_end-lat_start)*(Math.PI/180);  
      var dLng = (lng_end-lng_start)*(Math.PI/180); 
      var a = 
        Math.sin(dLat/2) * Math.sin(dLat/2) +
        Math.cos((lat_start)*(Math.PI/180)) * Math.cos((lat_end)*(Math.PI/180)) * 
        Math.sin(dLng/2) * Math.sin(dLng/2)
        ; 
      var c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1-a)); 
      var d = R * c * 1000; // Distance in m
      var t = d/0.8;
      //console.log('Distanz: ', d)

      this.distance_arr.push({
        id: allMuseumsData[i].properties.museum_id, 
        name: allMuseumsData[i].properties.name_museum,
        recom: allMuseumsData[i].properties.avg_prob_recom,
        position: allMuseumsData[i].geometry.coordinates,
        arrival_time: null,
        dist: Math.round(d),
        dist_time: t,
        attr: null,
        dwell: allMuseumsData[i].properties.avg_dwell,
        price: allMuseumsData[i].properties.avg_fee.toFixed(1).concat("0"),
        
      })
    }


    var day = preferenceData[0].weekday;
    var opening_day = day.concat('_open');
    var closing_day = day.concat('_close');
    //console.log(opening_day);
    for (var i = 0; i < mlength; i++) {
      var opening_time = allMuseumsData[i].properties[opening_day];
      //console.log(opening_time)

      opening_time = opening_time.split('T');
      opening_time = opening_time[1].split(':');
      var opening_time_sec = opening_time[0]*3600 + opening_time[1]*60;

      //console.log(opening_time_sec);
      var start_time = this.tourData[this.tourData.length-1].go_time;
      start_time = start_time.split(':');
      var start_time_sec = start_time[0]*3600 + start_time[1]*60;

      //console.log(start_time_sec + distance_arr[i].dist_time + 1800);
      //console.log(opening_time_sec);

      // Öffungszeit mit einem Puffer von 30 Minuten berücksichtigen
      if(start_time_sec + this.distance_arr[i].dist_time + 1800 < opening_time_sec) {
        allMuseumsData.splice(i, 1);
        this.distance_arr.splice(i, 1);
        i = i-1;
        mlength = mlength-1;
      }
    }

    for (var i = 0; i < mlength; i++) {
      var closing_time = allMuseumsData[i].properties[closing_day];
      //console.log(closing_time)

      closing_time = closing_time.split('T');
      closing_time = closing_time[1].split(':');
      var closing_time_sec = closing_time[0]*3600 + closing_time[1]*60;

      //console.log(closing_time_sec);
      var start_time = this.tourData[this.tourData.length-1].go_time;
      start_time = start_time.split(':');
      var start_time_sec = start_time[0]*3600 + start_time[1]*60;

      //console.log(start_time_sec + distance_arr[i].dist_time + 0.7*allMuseumsData[i].properties.avg_dwell*60);
      //console.log(closing_time_sec);

      // Kalkulieren mit einer ausreichenden Aufenthaltszeit von 70 % der durchschnittlichen Besuchzeit
      if(start_time_sec + this.distance_arr[i].dist_time + 0.7*allMuseumsData[i].properties.avg_dwell*60 > closing_time_sec) {
        allMuseumsData.splice(i, 1);
        this.distance_arr.splice(i, 1);
        i = i-1;
        mlength = mlength-1;
      }
    }

    for (var i = 0; i < mlength; i++) {
      var end_time = preferenceData[0].end_time;
      end_time = end_time.split(':');
      var end_time_sec = end_time[0]*3600 + end_time[1]*60;

      //console.log(end_time_sec);
      var start_time = this.tourData[this.tourData.length-1].go_time;
      start_time = start_time.split(':');
      var start_time_sec = start_time[0]*3600 + start_time[1]*60;

      //console.log(start_time_sec + distance_arr[i].dist_time + 0.7*allMuseumsData[i].properties.avg_dwell*60);
      //console.log(end_time_sec);

      // Wenn dem Besucher selbst zu wenig Zeit zur verfügung steht, damit er ein Museum 70% besuchen kann.
      if(start_time_sec + this.distance_arr[i].dist_time + 0.7*allMuseumsData[i].properties.avg_dwell*60 > end_time_sec) {
        allMuseumsData.splice(i, 1);
        this.distance_arr.splice(i, 1);
        i = i-1;
        mlength = mlength-1;
      }
    }
    // distance_arr-Zeile auch löschen, wenn ein Eintrag aus allMuseumsData gelöscht wird
    
    // 1970-01-01T12:00:00Z

    for (var i = 0; i < mlength; i++) {
      var tGrp = 0;
      var tGrpPreference = preferenceData[0].target_group;
      if (allMuseumsData[i].properties[tGrpPreference] == true) {
        tGrp = 1;
      }
      var reach = 5-5*Math.exp(-10*(1/(this.distance_arr[i].dist_time/60)));
      console.log(reach)
      var attr = 1.5*allMuseumsData[i].properties.avg_prob_recom + 2*tGrp + 1.25*reach + 0.01*allMuseumsData[i].properties.avg_dwell;
      this.distance_arr[i].attr = attr;
    }

    function compare(a, b) {
      const aattr = a.attr;
      const battr = b.attr;

      let comparison = -1;
      if (aattr < battr) {
        comparison = 1;
      }
      return comparison;
    }

    this.distance_arr.sort(compare);
    this.distance_arr.splice(5,40);

    //console.log(allMuseumsData);
    console.log(this.distance_arr);
    if (this.distance_arr.length == 0) {
      alert('Laut Ihren Präferenzen können keine (weiteren) Museen vorgeschlagen werden!')
      this.$router.push({ path: '/tour' })
    }
  },

    /*
    new Vue({
      el: "suggestions",
      data: {
        distance_arr: ''
      }
    });
    */

    //const app = new Vue({ template: '<div>Foo bar</div>' });
    /*for (var i = 0; i < distance_arr.length; i++) {
      
      const sugg = document.getElementById("calc_sugg");
      var ourDiv = document.createElement("div");

      
      
      ourDiv.style.height = "21px";
      ourDiv.style.textAlign = "left";
      ourDiv.style.textIndent = "5px";
      ourDiv.style.paddingTop = "5px";
      ourDiv.innerHTML = "Tolles Museum"
      console.log(ourDiv)
      //suggestions.calc_sugg.appendChild(ourDiv);
      sugg.appendChild(ourDiv);
      //app.$mount(ourDiv);
    };
    */
    /*
     * NÄCHSTE SCHRITTE
     *
     * - !!! (Startzeit des Nutzers + Wegzeit + 30 min < Öffnungszeit) ist ein sehr einschränkendes Kriterium !!!
     Gewichtung mit Sollkriterien (Gewichtung mit Distanz nochmal anschauen) -> Wartezeit bis zur Öffnung in Gewichtung einbeziehen
     Tabelle nach Attraktivität sortieren
     * - Beste 5 Museen dem Nutzer zur Auswahl anzeigen
     * - Hinzufügen des gewählten Museums zu tourData
     * - Karte mit Museen und Popups einbetten
     * - Verknüpfungen der Vues, Buttons, Löschen von Objekten mit Sicherheitsabfrage
     * - "Über die App"-Vue anpassen
     * - Visuelle Feinabstimmung
     * - (Vue mit Liste aller Museen)
     * 
     */
      
      
      
      
      
      
      
      
      
      
      
      
      
      
      
      
      
      
      /*
      Kopie "museumCopy" der allMuseumsData Tabelle machen

      *** FILTER-MUSSKRITERIEN ***

        for (i=0, i<museumCopy.rows.length, i++){
          if(i[Bewertung]) <= 5: delete i
          if Barrierefreiheit vorhanden wenn erforderlich, sonst delete
          if mind. 1 Benutzerkategorie in Museumskategorie, sonst delete
          if Museum ist bereits in tourData: delete
        }
      
      *** ERREICHBARE MUSEEN ***
        
        for (i=0, i<museumCopy.rows.length, i++){
          if Museum hat am gewünschten Wochentag geöffnet, sonst delete

        Wenn tourData leer, Startpunkt ist Standort aus preferenceData, sonst Standort des letzten Museums in tourData
        Distanz zwischen Startpunkt und jedem noch vorhandenen Museum berechnen
        Gehzeit berechnen: Distanz/Geschwindigkeit (0.8 m/s) 

        Hat das Museum schon offen, wenn man dort ankommt?
        Schliesst das Museum, bevor man es zu 70% besucht hat?
        Hat der Benutzer selbst noch Zeit, ein Museum zu 70% zu besuchen?

      *** GEWICHTUNG MIT SOLLKRITERIEN ***

        Attraktivitätspunkte für noch übrige Museen vergeben
        Tabelle nach Attraktivität sortieren!
      */
    
  }
}
</script>

